.card {
  background: #fff;
  border-radius: 15px;
  height: 200px;
  line-height: 200px;
  margin: 0.75rem;
  position: relative;
  /* width: 325px; */
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background:rgb(220, 194, 108);
  border-radius: 15px;
}
.card > .img-container > img {
  width: 75%;
}

.navbar-background {
  background-color: white;
  border-radius: 15px;
  padding: 3px;
}

.navbar {
  width: 100%;
  background-color: rgb(131, 204, 71);
  border-radius: 15px;
}
.wrapper {
  padding-top: 50px;
  background-image: url("http://s-tlk.org/news/tlg_background1.jpg");
  background-size: cover;
  background-position: center;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 20px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
}

html, body {
  height: 100%;
  width: 100%;
  margin: 0;
}

body {
  background-image: url("http://s-tlk.org/news/tlg_background1.jpg");
  background-size: cover;
  background-position: center;
  overflow: hidden;
}

#menu {
  height: 100%;
  width: 100%;
}

#menu p {
  margin: 0;
}

.title {
  width: 100%;
  text-align: center;
  margin-bottom: 50px;
}

* {
  box-sizing: border-box;
  font-family: 'Acme', sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

